.UserCard {
    background: #fff5f5;
    border-radius: 4px;
    box-shadow: 3px 3px 7px 0 #9d9d9f;
    padding: 10px;
    width: 400px;
    display: grid;
    grid-column-gap: 6px;
    grid-template-columns: 1fr 1fr;
 }
 
 .Title {
    font-weight: bold;
 }
 
 .rank {
    margin-right: 4px;
 }

 .buttons {
  background-color: blue;
 }